import isEmpty from 'utils/coreDupes/isEmpty';
import isObject from 'utils/coreDupes/isObject';

/**
 * Email Address
 */

/**
 * Takes a number of email address response types from a LinkedIn response and returns just the email address
 * @param {Array.<Object>} emails - Several different objects with a nested email address
 * @param {string} primaryEmail - When sometimes an email address is passed down without other LinkedIn display data
 * @returns {string} The deduced email address
 */
export function formatEmailAddress(emails, primaryEmail) {
	try {
		let emailAddress;

		if (primaryEmail?.length) {
			emailAddress = primaryEmail;
		} else if (emails && typeof emails === 'string' && emails.length) {
			emailAddress = emails;
		} else if (emails?.emailAddress) {
			emailAddress = emails.emailAddress;
		} else if (Array.isArray(emails) && emails.length) {
			emailAddress = emails[0].emailAddress;
		}

		return emailAddress;
	} catch (err) {
		return null;
	}
}

/**
 * Location
 */

export const cleanLocationString = (locationString) => {
	return (
		locationString
			// Replace line breaks
			.replace(/(\r\n|\n|\r)/gm, ' ')
			.replace(/^Greater /, '')
			.replace(/ Area$/, '')
			.replace(/ Metropolitan$/, '')
	);
};

const LOCATION_OBJECT_KEYS = [
	'line1',
	'line2',
	'city',
	'geographicArea',
	'postalCode',
	'country',
];
function hasValidLocationKeys(location) {
	return (
		isObject(location) &&
		!isEmpty(location) &&
		Object.keys(location).some((k) => LOCATION_OBJECT_KEYS.includes(k))
	);
}

export function formatCompanyLocationObject(location) {
	try {
		let street1;
		let street2;
		let city;
		let state;
		let postCode;
		let country;

		if (location && hasValidLocationKeys(location)) {
			({
				line1: street1,
				line2: street2,
				city,
				geographicArea: state,
				postalCode: postCode,
				country,
			} = location);
		}

		return {
			street1,
			street2,
			city,
			state,
			postCode,
			country,
		};
	} catch (err) {
		return null;
	}
}

export function getCompanyLocationStringFromObject(locationObject) {
	try {
		let fullString;
		if (!isEmpty(locationObject)) {
			const {
				street1,
				street2 = '',
				city,
				state,
				postCode,
				country,
			} = locationObject;
			fullString = [street1, street2, city, state, postCode, country]
				.map((s) => s && s.trim())
				.filter((s) => !!s)
				.join(' ');
		}

		if (typeof fullString === 'string' && !fullString.length) {
			fullString = undefined;
		}

		return fullString;
	} catch (err) {
		return null;
	}
}

/**
 * Social
 */

export function formatContactSocial({
	websites = [],
	socialHandles = [],
	twitterHandles = [],
}) {
	try {
		let social = [];

		if (websites?.length) {
			const contactWebsites = websites
				.map(
					(w) =>
						w && {
							type: w.type?.category
								? w.type.category
								: undefined,
							url: w.url,
						}
				)
				.filter((w) => !!w);
			if (contactWebsites?.length) {
				contactWebsites.forEach((website) => social.push(website));
			}
		}

		if (socialHandles?.length) {
			const contactSocialHandles = socialHandles.map(
				(t) =>
					t?.type?.length &&
					t.type.toLowerCase() === 'twitter' && {
						type: 'social',
						socialType: 'twitter',
						url: `http://twitter.com/${t.name}`,
					}
			);

			if (contactSocialHandles?.length) {
				social.push(contactSocialHandles);
			}
		}

		if (twitterHandles?.length) {
			const contactTwitterHandles = twitterHandles.map(
				(t) =>
					t?.name && {
						type: 'social',
						socialType: 'twitter',
						url: t.name.includes('http')
							? t.name
							: `http://twitter.com/${t.name}`,
					}
			);
			if (contactTwitterHandles?.length) {
				social.push(contactTwitterHandles);
			}
		}

		// if (picture && picture.length) {
		// 	social.push({
		// 		type: 'image',
		// 		url: picture,
		// 		socialType: 'LinkedIn',
		// 	});
		// }

		if (social?.length) {
			social = social.filter((s) => s && s.url);
		}

		return social;
	} catch (err) {
		return null;
	}
}
