const VALID_CONTACT_PRIMARY_PROPERTIES = ['firstName', 'contactName'];
const VALID_CONTACT_SECONDARY_PROPERTIES = [
	'liRecruiterId',
	'liSalesNavId',
	'liPublicIdentifier',
];

const isValidContact = (contact) => {
	return (
		VALID_CONTACT_PRIMARY_PROPERTIES.some((prop) =>
			Object.prototype.hasOwnProperty.call(contact, prop)
		) &&
		VALID_CONTACT_SECONDARY_PROPERTIES.some((prop) =>
			Object.prototype.hasOwnProperty.call(contact, prop)
		) &&
		VALID_CONTACT_PRIMARY_PROPERTIES.every(
			(prop) => !contact[prop]?.toLowerCase().includes('linkedin member')
		)
	);
};

export default isValidContact;
