import { getMemberIdFromUrn } from 'workers/processExtractedData/utils/identifiers';
import ensureArray from '@seamless/core/src/utils/ensureArray';
import { cleanLocationString } from 'utils/data';

const formatContactPosition = (position) => {
	const {
		company,
		companyName,
		companyUrn,
		companyUrnResolutionResult,
		companyResolutionResult,
		current,
		dateRange,
		description,
		// Used for filtering out duplicates and not filtering out the rare type of same position
		// but one being "contract" to "full-time", ie. https://www.linkedin.com/in/alyssa-muscarello/
		'*employmentType': employmentType,
		endDateOn,
		endedOn,
		location,
		locationName,
		startDateOn,
		startedOn,
		title,
	} = position;

	const { industry, location: companyUrnLocation } =
		companyUrnResolutionResult || {};

	const { industries: _industries } = companyResolutionResult || {};

	const {
		start: _dateRangeStart,
		startDate: _dateRangeStartDate,
		end: _dateRangeEndDate,
		endDate: _dateRangeEnd,
	} = dateRange || {};

	const _companyUrn =
		(company && typeof company === 'string' && company) || companyUrn;

	const startDate =
		_dateRangeStart || _dateRangeStartDate || startDateOn || startedOn;
	const endDate = _dateRangeEnd || _dateRangeEndDate || endDateOn || endedOn;

	let _location = location?.displayName ?? location;
	if (!_location && companyUrnLocation) {
		_location = companyUrnLocation;
	}

	if (!_location && locationName) {
		_location = locationName;
	}

	let industries;
	if (_industries) {
		industries = ensureArray(_industries);
	} else if (industry) {
		industries = ensureArray(industry);
	}

	return {
		companyName: companyName?.trim(),
		current,
		description,
		employmentType,
		endDate:
			endDate &&
			typeof endDate === 'object' &&
			Object.keys(endDate).length
				? {
						...(endDate.month && { month: endDate.month }),
						...(endDate.year && { year: endDate.year }),
				  }
				: null,
		fullString: _location && cleanLocationString(_location),
		industries:
			industries && Array.isArray(industries) && industries.length
				? industries
				: null,
		linkedInCompanyId: _companyUrn && getMemberIdFromUrn(_companyUrn),
		startDate:
			startDate &&
			typeof startDate === 'object' &&
			Object.keys(startDate).length
				? {
						...(startDate.month && { month: startDate.month }),
						...(startDate.year && { year: startDate.year }),
				  }
				: null,
		titleName: title,
	};
};

const formatOccupationSnippet = (snippetText) => {
	let occupation;

	const occupationParts = snippetText.match(
		/^current:\s(.*)(\s-\s\.\.\..*)?$/i
	);
	if (occupationParts?.length) {
		[, occupation] = occupationParts;
	}

	return occupation;
};

/**
 * Parses an occupation string into company name and title
 * @param {String} occupation
 * @return {Object} { companyName, title }
 */
const parsePositionFromOccupationString = (occupation) => {
	const occupationParts =
		occupation &&
		((occupation.indexOf(' at ') > -1 && occupation.split(' at ')) ||
			(occupation.indexOf(' @ ') > -1 && occupation.split(' @ ')) ||
			(occupation.indexOf(' - ') > -1 && occupation.split(' - ')));

	let companyName;
	let title;

	if (!occupationParts) {
		title = occupation;
	} else {
		companyName =
			!occupationParts?.length || occupationParts.length === 1
				? undefined
				: occupationParts.pop();
		title = !occupationParts?.length
			? undefined
			: occupationParts.join(' ');
	}

	return { companyName, title };
};

const getPositionsFromGroupedWorkExperience = (groupedWorkExperience) => {
	const positions = groupedWorkExperience.reduce(
		(groupedPositionsArr, arrItem) => {
			let positionsArr = groupedPositionsArr;
			const groupPositions = arrItem.positions;
			positionsArr = [...(positionsArr || []), ...(groupPositions || [])];
			return positionsArr;
		},
		[]
	);
	return positions;
};

export {
	formatContactPosition,
	formatOccupationSnippet,
	parsePositionFromOccupationString,
	getPositionsFromGroupedWorkExperience,
};
