import { getSalesNavIdFromUrn } from 'workers/processExtractedData/utils/identifiers';
import createSeamlessContact from 'workers/processExtractedData/utils/createSeamlessContact';
import isValidContact from 'utils/isValidContact';

const parsePremiumSavedLeads = (data) => {
	const {
		currentPositions,
		entityUrn,
		geoRegion,
		firstName,
		fullName,
		lastName,
		profilePictureDisplayImage,
	} = data;

	const positions =
		Array.isArray(currentPositions) && currentPositions.length
			? currentPositions
			: [];

	const contactOpts = {
		salesNavIdentifier: entityUrn && getSalesNavIdFromUrn(entityUrn),
		firstName,
		lastName,
		fullName,
		profilePicture: profilePictureDisplayImage,
		location: geoRegion,
		positions: positions?.length ? positions : null,
	};

	const parsedContact = createSeamlessContact(contactOpts);
	parsedContact.pageSectionSourced = 'premiumSavedLeads';
	parsedContact.itemType = 'contact';
	return isValidContact(parsedContact) ? parsedContact : null;
};

export default parsePremiumSavedLeads;
