const formatContactIndustries = (industries = []) => {
	try {
		let formattedIndustries = industries;
		if (
			formattedIndustries &&
			typeof formattedIndustries === 'object' &&
			!Array.isArray(formattedIndustries)
		) {
			formattedIndustries = Object.values(formattedIndustries);
		}

		formattedIndustries = formattedIndustries.reduce((arr, ind) => {
			const industry = (
				(typeof ind === 'string' && ind) ||
				ind.name ||
				''
			).trim();
			if (industry) {
				arr.push(industry);
			}
			return arr;
		}, []);
		return formattedIndustries.length ? formattedIndustries : null;
	} catch (err) {
		return null;
	}
};

export default formatContactIndustries;
