const cleanEmptyNullData = (data) => {
	if (!data) {
		return {};
	}

	const cleanedData = data;
	Object.entries(cleanedData).forEach(([k, v]) => {
		if (v && typeof v === 'object') {
			cleanEmptyNullData(v);
		}

		if (
			(v && typeof v === 'object' && !Object.keys(v).length) ||
			v === null ||
			v === undefined
		) {
			if (Array.isArray(cleanedData)) {
				cleanedData.splice(k, 1);
			} else {
				delete cleanedData[k];
			}
		}
	});

	return cleanedData;
};

const cleanLocationString = (locationString) => {
	return (
		locationString
			// Replace line breaks
			.replace(/(\r\n|\n|\r)/gm, ' ')
			.replace(/^Greater /, '')
			.replace(/ Area$/, '')
			.replace(/ Metropolitan$/, '')
	);
};

const cleanUrl = (url) => {
	let newUrl = url && typeof url === 'string' && url.trim();
	if (!newUrl) {
		// return original data
		return url;
	}

	if (/^https?:\/([^/].*|\/\/+.+)$/i.test(newUrl)) {
		newUrl = newUrl.replace(/:\/+/, '://');
	}

	return newUrl;
};

export { cleanEmptyNullData, cleanLocationString, cleanUrl };
