import { expose } from 'comlink';
import parsePremiumSavedLeads from 'workers/processExtractedData/processPremiumSavedLeadsData/parsePremiumSavedLeads';

function hasSavedLeadsData(data) {
	return data.elements?.some?.((element) => element.saved === true);
}

const processPremiumSavedLeadsData = async (pageData) => {
	let parsedItems = [];
	let contactParsers = [];

	if (hasSavedLeadsData(pageData)) {
		contactParsers = pageData.elements.map((item) =>
			parsePremiumSavedLeads(item)
		);
	}

	if (contactParsers.length) {
		parsedItems = await Promise.all(contactParsers);
	}

	return {
		parsedItems,
	};
};

expose(processPremiumSavedLeadsData, self);
