// Vanilla JS equivalent to Lodash _uniqBy
const uniqueBy = (arr, keys) => {
	const obj = arr.reduce((ac, a) => {
		const keyString = keys.map((k) => a[k]).join('-');
		const current = ac;
		current[keyString] = a;
		return current;
	}, {});

	return Object.values(obj);
};

export default uniqueBy;
