import { formatEmailAddress } from 'workers/processExtractedData/utils/contactInfo';
import formatProfilePicture from 'workers/processExtractedData/utils/formatProfilePicture';
import formatContactIndustries from 'workers/processExtractedData/utils/industries';
import {
	formatContactEducations,
	formatContactLanguages,
	formatContactSkills,
} from 'workers/processExtractedData/utils/experience';
import {
	formatContactPosition,
	parsePositionFromOccupationString,
} from 'workers/processExtractedData/utils/occupation';
import { cleanEmptyNullData, cleanLocationString, cleanUrl } from 'utils/data';
import getCurrentPositions from 'utils/getCurrentPositions';
import hasResults from 'utils/hasResults';
import isValidString from 'utils/isValidString';
import uniqueBy from 'utils/collection';

// TODO: move these to a config file
const STANDARD_CONTACT_PROFILE_URL_BASE = 'https://www.linkedin.com/in/';
const PREMIUM_CONTACT_PROFILE_URL_BASE = 'https://www.linkedin.com/sales/lead/';
const RECRUITER_CONTACT_PROFILE_URL_BASE =
	'https://www.linkedin.com/talent/search/profile/';

function setCompanyNameAndTitle(profile) {
	const _profile = profile;

	let companyName;
	let title;
	if (hasResults(_profile.positions)) {
		const currentPositions = getCurrentPositions(_profile.positions);
		if (hasResults(currentPositions)) {
			_profile.companyName = currentPositions[0].companyName;
			_profile.title = currentPositions[0].titleName;
		}
	}

	if ((!_profile.companyName || !_profile.title) && _profile.occupation) {
		({ companyName, title } = parsePositionFromOccupationString(
			_profile.occupation
		));
		if (companyName) {
			_profile.companyName = companyName;
		}

		if (title) {
			_profile.title = title;
		}
	}

	if (hasResults(_profile.positions)) {
		const currentPositions = getCurrentPositions(_profile.positions);
		if (
			hasResults(currentPositions) &&
			(_profile.companyName !== currentPositions[0].companyName ||
				_profile.title !== currentPositions[0].titleName)
		) {
			_profile.companyName = currentPositions[0].companyName;
			_profile.title = currentPositions[0].titleName;
		}
	}

	return _profile;
}

/**
 * Format the data from a parsed contact object into a Seamless.AI contact object
 * @param {Object} data - Object of parsed contact data
 * @return {Object} Seamless.AI expected contact object format
 */
const createSeamlessContact = (data) => {
	const {
		// LinkedIn Identifiers
		liMemberId,
		publicIdentifier,
		salesNavIdentifier,
		recruiterIdentifier,

		// LinkedIn Profile URLs
		flagshipProfileUrl,

		// Entity info
		firstName,
		lastName,
		fullName,
		birthDateOn,
		profilePicture,
		summary,

		// Contact info
		emails,
		phoneNumbers,
		primaryEmail,
		location,
		social,

		// Personal info
		awards,
		certifications,
		courses,
		educations,
		industries,
		languages,
		organizations,
		patents,
		projects,
		publications,
		skills,
		volunteeringExperiences,

		// JobInfo
		occupation,
		positions,
	} = data;

	let contactName = fullName;
	if (!contactName?.length && firstName && lastName) {
		contactName = `${firstName} ${lastName}`;
	}
	const liProfileUrl =
		flagshipProfileUrl ||
		(publicIdentifier &&
			`${STANDARD_CONTACT_PROFILE_URL_BASE}${publicIdentifier}`);
	const liSalesNavUrl =
		salesNavIdentifier &&
		`${PREMIUM_CONTACT_PROFILE_URL_BASE}${salesNavIdentifier}`;
	const liRecruiterUrl =
		recruiterIdentifier &&
		`${RECRUITER_CONTACT_PROFILE_URL_BASE}${recruiterIdentifier}`;

	let _positions = hasResults(positions)
		? positions.map(
				(position) =>
					typeof position === 'object' &&
					Object.keys(position).length &&
					formatContactPosition(position)
		  )
		: null;

	if (hasResults(_positions)) {
		// remove empty
		_positions = _positions.filter(
			(pos) => !!pos?.companyName && !!pos?.titleName
		);
		_positions = uniqueBy(_positions, [
			'companyName',
			'titleName',
			'employmentType',
		]);
	}

	let companyName;
	let title;

	if (occupation) {
		({ companyName, title } =
			parsePositionFromOccupationString(occupation));
	}

	if (!companyName && positions?.length) {
		const latestPosition = positions[0]; // the latest position may not always be the first one. if not, use startDate
		companyName = latestPosition.companyName;
	}

	let person = {
		// LinkedIn Identifiers
		liMemberId,
		liPublicIdentifier: publicIdentifier && decodeURI(publicIdentifier),
		liSalesNavId: salesNavIdentifier,
		liRecruiterId: recruiterIdentifier,

		// LinkedIn Profile URLs
		liProfileUrl: isValidString(liProfileUrl)
			? cleanUrl(liProfileUrl)
			: null,
		liSalesNavUrl: isValidString(liSalesNavUrl)
			? cleanUrl(liSalesNavUrl)
			: null,
		liRecruiterUrl: isValidString(liRecruiterUrl)
			? cleanUrl(liRecruiterUrl)
			: null,

		// Entity Info
		firstName,
		lastName,
		contactName,
		birthday: birthDateOn,
		liImageUrl: profilePicture && formatProfilePicture(profilePicture),

		// Contact info
		contactEmail: formatEmailAddress(emails, primaryEmail),
		phoneNumbers,
		locationFullString: isValidString(location)
			? cleanLocationString(location)
			: null,
		social,

		// Personal info
		...(summary && { meta: { summary } }),
		awards: hasResults(awards) ? awards : null,
		certifications: hasResults(certifications) ? certifications : null,
		courses: hasResults(courses) ? courses : null,
		educations: hasResults(educations)
			? formatContactEducations(educations)
			: null,
		industries: formatContactIndustries(industries),
		languages: hasResults(languages)
			? formatContactLanguages(languages)
			: null,
		organizations: hasResults(organizations) ? organizations : null,
		patents: hasResults(patents)
			? patents.filter((patent) => !!patent)
			: null,
		projects: hasResults(projects)
			? projects.filter((project) => !!project)
			: null,
		publications: hasResults(publications)
			? publications.filter((publication) => !!publication)
			: null,
		skills: hasResults(skills) ? formatContactSkills(skills) : null,
		volunteeringExperiences: hasResults(volunteeringExperiences)
			? volunteeringExperiences
			: null,

		// Job info
		companyName,
		title,
		// company: {
		// 	name: companyName,
		// 	title,
		// },
		occupation,
		positions: _positions,
		itemType: 'contact',
	};

	person = setCompanyNameAndTitle(person);
	person = cleanEmptyNullData(person);
	return person;
};

export default createSeamlessContact;
