import ensureArray from '@seamless/core/src/utils/ensureArray';

export function formatContactEducations(educations) {
	try {
		return educations
			.filter((education) => education)
			.map((education) => {
				const {
					schoolName: name,
					degree,
					degreeName,
					activities = [],
					fieldOfStudy,
					dateRange: {
						start: { year: dateRangeStartYear } = {},
						end: { year: dateRangeEndYear } = {},
					} = {},
					startedOn: { year: startedOnYear } = {},
					endedOn: { year: endedOnYear } = {},
					endDateOn: { year: endDateOnYear } = {},
					startDateOn: { year: startDateOnYear } = {},

					// Legacy Recruiter
					startDateYear,
					endDateYear,
				} = education;

				const startYear =
					dateRangeStartYear ||
					startedOnYear ||
					startDateOnYear ||
					startDateYear;
				const endYear =
					dateRangeEndYear ||
					endedOnYear ||
					endDateOnYear ||
					endDateYear;

				let { fieldsOfStudy } = education;
				if (Array.isArray(fieldsOfStudy) && fieldsOfStudy.length) {
					fieldsOfStudy = fieldsOfStudy.join();
				}

				const educationFieldOfStudy =
					typeof fieldsOfStudy === 'string'
						? fieldsOfStudy
						: fieldOfStudy;

				const formattedEducation = {
					companyName: name,
					degreeName: degreeName || degree,
					activities: activities && ensureArray(activities),
					fieldOfStudy: educationFieldOfStudy,
					...(startYear && { startDate: { year: startYear } }),
					...(endYear && { endDate: { year: endYear } }),
				};

				return formattedEducation;
			});
	} catch (err) {
		return null;
	}
}

export function formatContactSkills(skills = []) {
	try {
		const contactSkills = skills.reduce((skillsArr, skill) => {
			const _skills = skillsArr;
			if (skill.name) {
				_skills.push(skill.name);
			}
			return _skills;
		}, []);
		return contactSkills;
	} catch (err) {
		return null;
	}
}

export function formatContactLanguages(languages = []) {
	try {
		const contactLanguages = languages.reduce((languagesArr, language) => {
			const _languages = languagesArr;
			if (language.name) {
				_languages.push(language.name);
			}
			return _languages;
		}, []);
		return contactLanguages;
	} catch (err) {
		return null;
	}
}
