// ie., https://www.linkedin.com/in/annieheckenberger/
const STANDARD_CONTACT_PROFILE_URL_PUBLIC_ID_REG_EXP =
	/(?:https?:)?(?:\/\/)?(?:[\w]+\.)?(?:linkedin\.com)?\/in\/(?<publicIdentifier>[\w_À&&-ÿ%-][^/?]+)\/?/i;

// Examples:
// https://www.linkedin.com/sales/company/130757/insights?_ntb=fAcMJ6%2FtREiNqQyKnKT4Xg%3D%3D
// https://www.linkedin.com/sales/people/ACwAAAAmVFgBoPQGSO_l_3cwHlpNdOiHdaW2pAQ,NAME_SEARCH,L7q4?_ntb=%C2%A6%15O%09%C3%92%C2%AAA%C2%B0%C2%AF%C3%90%20C%C3%BFk%1D%C2%9A
// https://www.linkedin.com/sales/lead/ACwAAAAmVFgBoPQGSO_l_3cwHlpNdOiHdaW2pAQ,NAME_SEARCH,L7q4?_ntb=%C2%A6%15O%09%C3%92%C2%AAA%C2%B0%C2%AF%C3%90%20C%C3%BFk%1D%C2%9A
const PREMIUM_CONTACT_PROFILE_URL_PUBLIC_ID_REG_EXP =
	/(?:https?:)?(?:\/\/)?(?:[\w]+\.)?(?:linkedin\.com)?\/sales\/(people|lead)\/(?<publicIdentifier>[\w_À&-ÿ%-][^,?/]+)?/i;

// ie., https://www.linkedin.com/company/saatchi-&-saatchi-wellness/
const STANDARD_COMPANY_PROFILE_URL_PUBLIC_ID_REG_EXP =
	/(?:https?:)?(?:\/\/)?(?:[\w]+\.)?(?:linkedin\.com)?\/company\/(?<publicIdentifier>[\w_À&-ÿ%-][^/?]+)\/?/i;

// ie., /sales/company/610608?_ntb=fAcMJ6%2FtREiNqQyKnKT4Xg%3D%3D
const PREMIUM_COMPANY_PROFILE_URL_PUBLIC_ID_REG_EXP =
	/(?:https?:)?(?:\/\/)?(?:[\w]+\.)?(?:linkedin\.com)?\/sales\/company\/(?<publicIdentifier>[\d][^/?]+)\/?/i;

// Examples:
// https://www.linkedin.com/talent/profile/AEMAAAB_kGABaRsOMBn6bfW1iw_90slztzfmdys?project=400938220&lipi=urn%3Ali%3Apage%3Ad_talent_projectsHome%3B%2BMSr4USfTHyZnlrgcgRpVw%3D%3D
// /talent/profile/AEMAAAB_kGABaRsOMBn6bfW1iw_90slztzfmdys?project=400938220&lipi=urn%3Ali%3Apage%3Ad_talent_projectsHome%3B%2BMSr4USfTHyZnlrgcgRpVw%3D%3D
// https://www.linkedin.com/talent/hire/400938220/manage/all/profile/AEMAAAEzgEcBNTs49BJySGuQVAAZ2K9CECIwpT0?project=400938220
const RECRUITER_CONTACT_PROFILE_URL_PUBLIC_ID_REG_EXP =
	/(?:\/profile)\/(?<publicIdentifier>[\w_À&-ÿ%-][^?/]+)\/?/i;

// urn:li:fs_salesCompany:76390
// urn:li:member:159784268286
// urn:li:fsd_entityResultViewModel:(urn:li:fsd_company:3776,SEARCH_SRP)
const GET_MEMBER_ID_FROM_URN_REG_EXP =
	/(?:\w+:(\(?))+(?::[\w_]+(?:member|company):)?(?<memberId>[\d]+)?/i;

// Examples:
// urn:li:fs_salesProfile:(ACwAAABXB8YBL-pY0m1uk-T5PnxhaGO1zKqmc8U,NAME_SEARCH,ecVP)
// urn:li:fsd_entityResultViewModel:urn:li:fsd_profile:ACoAAA2xYEwBskLoVyr5rCQ4PWpslakEhwAtgXc // New Community search result entityUrn
// urn:li:fsd_profileCard:(ACoAAAAD8nEBVA4J_Y8kdohsFvb49-Gritg17kQ,EXPERIENCE,en_US)
const SALES_NAV_ID_FROM_URN_REG_EXP =
	/(?:\w+:)+fs(?:d)?_(?:sales|mini|profile)?(Profile|Card):(?:\()?(?<salesNavId>[\w_À&-ÿ%-][^,]+)?,?(?<type>.[^,]+)?/i;

// ie., urn:li:ts_profile:AEMAAABRsqUB-vVYVqCYoLwKjsFNBZFF_I4IYdw
const RECRUITER_CONTACT_FROM_URN_REG_EXP =
	/(?:\w+:)+(ts_profile|ts_linkedin_member_profile):\(?(?<recruiterId>[\w_À&-ÿ%-][^,?]+)?/i;

// TODO: this is getting big, refactor...how to pull key matchers
// from central location instead of appending here
const profileUrlIdModuleRegexMap = {
	premiumCompanySearch: PREMIUM_COMPANY_PROFILE_URL_PUBLIC_ID_REG_EXP,
	premiumCompanyProfile: PREMIUM_COMPANY_PROFILE_URL_PUBLIC_ID_REG_EXP,
	premiumSavedAccounts: PREMIUM_COMPANY_PROFILE_URL_PUBLIC_ID_REG_EXP,
	premiumCompanyRecommendedLeads:
		PREMIUM_COMPANY_PROFILE_URL_PUBLIC_ID_REG_EXP,
	premiumCompanyAccountMapEntries:
		PREMIUM_COMPANY_PROFILE_URL_PUBLIC_ID_REG_EXP,
	premiumCompanyPeopleAlsoViewed:
		PREMIUM_COMPANY_PROFILE_URL_PUBLIC_ID_REG_EXP,

	premiumSavedLeads: PREMIUM_CONTACT_PROFILE_URL_PUBLIC_ID_REG_EXP,
	premiumContactSavedSearch: PREMIUM_CONTACT_PROFILE_URL_PUBLIC_ID_REG_EXP,
	premiumContactSearch: PREMIUM_CONTACT_PROFILE_URL_PUBLIC_ID_REG_EXP,
	premiumContactProfile: PREMIUM_CONTACT_PROFILE_URL_PUBLIC_ID_REG_EXP,
	premiumContactRecommendations:
		PREMIUM_CONTACT_PROFILE_URL_PUBLIC_ID_REG_EXP,
	premiumContactRecommendedLeads:
		PREMIUM_CONTACT_PROFILE_URL_PUBLIC_ID_REG_EXP,
	premiumContactSimilarLeads: PREMIUM_CONTACT_PROFILE_URL_PUBLIC_ID_REG_EXP,

	standardCompanySearch: STANDARD_COMPANY_PROFILE_URL_PUBLIC_ID_REG_EXP,
	standardCompanyProfile: STANDARD_COMPANY_PROFILE_URL_PUBLIC_ID_REG_EXP,
	standardCompanyPeopleYouMayKnow:
		STANDARD_CONTACT_PROFILE_URL_PUBLIC_ID_REG_EXP,
	standardBlendedSearchCompany:
		STANDARD_COMPANY_PROFILE_URL_PUBLIC_ID_REG_EXP,

	standardContactProfile: STANDARD_CONTACT_PROFILE_URL_PUBLIC_ID_REG_EXP,
	standardContactPeopleYouMayKnow:
		STANDARD_CONTACT_PROFILE_URL_PUBLIC_ID_REG_EXP,
	standardContactPeopleAlsoViewed:
		STANDARD_CONTACT_PROFILE_URL_PUBLIC_ID_REG_EXP,
	standardContactRecommendationsReceived:
		STANDARD_CONTACT_PROFILE_URL_PUBLIC_ID_REG_EXP,
	standardContactRecommendationsGiven:
		STANDARD_CONTACT_PROFILE_URL_PUBLIC_ID_REG_EXP,
	standardContactSearch: STANDARD_CONTACT_PROFILE_URL_PUBLIC_ID_REG_EXP,
	standardBlendedSearchContact:
		STANDARD_CONTACT_PROFILE_URL_PUBLIC_ID_REG_EXP,

	recruiterContactProfile: RECRUITER_CONTACT_PROFILE_URL_PUBLIC_ID_REG_EXP,
	recruiterContactRecommendations:
		RECRUITER_CONTACT_PROFILE_URL_PUBLIC_ID_REG_EXP,
	recruiterContactSimilarProfiles:
		RECRUITER_CONTACT_PROFILE_URL_PUBLIC_ID_REG_EXP,
	recruiterContactSearch: RECRUITER_CONTACT_PROFILE_URL_PUBLIC_ID_REG_EXP,
	recruiterSavedContactSearch:
		RECRUITER_CONTACT_PROFILE_URL_PUBLIC_ID_REG_EXP,
};

export function getIdFromProfileUrl(profileUrl, module) {
	if (!profileUrl || !module) {
		return null;
	}

	const matchRegExp = profileUrlIdModuleRegexMap[module];
	const publicIdentifierMatchObj = profileUrl.match(matchRegExp);
	const profileId = publicIdentifierMatchObj?.groups?.publicIdentifier;
	return profileId ? decodeURI(profileId) : null;
}

export function getMemberIdFromUrn(urn) {
	const memberIdMatchObj = urn.match(GET_MEMBER_ID_FROM_URN_REG_EXP);
	return memberIdMatchObj?.groups?.memberId;
}

export function getSalesNavIdFromUrn(urn) {
	const salesNavIdMatchObj = urn.match(SALES_NAV_ID_FROM_URN_REG_EXP);
	return salesNavIdMatchObj?.groups?.salesNavId;
}

export function getRecruiterIdFromUrn(urn) {
	const recruiterIdMatchObj = urn.match(RECRUITER_CONTACT_FROM_URN_REG_EXP);
	return recruiterIdMatchObj?.groups?.recruiterId;
}

// parses the salesnav style urn now used in new li basic graphQL data to get the id and data type from it.
// creating this as a separate function from getSalesNavIdFromUrn for now as we try to move towards a new
// structure of processing the data.
export function parseElementUrn(urn) {
	const elementUrnMatchObj = urn.match(SALES_NAV_ID_FROM_URN_REG_EXP);
	const { salesNavId, type } = elementUrnMatchObj?.groups || {};
	return {
		liSalesNavId: salesNavId || '',
		elementType: type || '',
	};
}
