import { cleanUrl } from 'utils/data';

const formatProfilePicture = (profilePicture) => {
	try {
		let picture = profilePicture;

		if (picture && typeof picture === 'object') {
			const { artifacts, rootUrl } = picture;
			if (
				rootUrl?.length &&
				rootUrl.includes('http') &&
				artifacts &&
				Array.isArray(artifacts)
			) {
				picture =
					rootUrl +
					artifacts[artifacts.length - 1]
						.fileIdentifyingUrlPathSegment;
			} else if (
				(!rootUrl || !rootUrl.length) &&
				artifacts?.length &&
				artifacts[artifacts.length - 1].fileIdentifyingUrlPathSegment &&
				artifacts[
					artifacts.length - 1
				].fileIdentifyingUrlPathSegment.includes('http')
			) {
				picture =
					artifacts[artifacts.length - 1]
						.fileIdentifyingUrlPathSegment;
			}

			if (!picture || !picture.length) {
				return undefined;
			}
		}

		picture = picture && cleanUrl(picture);
		return picture;
	} catch (err) {
		return undefined;
	}
};

export default formatProfilePicture;
